.link {
    display: inline-flex;
    align-items: center;
    margin-top: var(--spacing-medium);
    font-weight: var(--font-bold);
    color: var(--color-background);
    text-decoration: none;
}
.link__img {
    width: 30px;
    height: 30px;
    margin-right: var(--spacing-small);
    object-fit: cover;
    object-position: center center;
}
